form {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    width: 90%;
    max-width: 300px;
    background-color: #fff;
    padding: 33px 40px;
}

label{
    text-align: center;
    font-weight: bold;
    font-size: 20px;
    color: #1E1E1F;
    margin-bottom: 26px;
}

textarea{
    background-color: #f9FBFC;
    border: 2px solid #202329;
    border-radius: 2px;
    box-shadow: 0px 5pc 50px rgba(31, 35, 41, 0.25);
    color: #1E1E1F;
    font-size: 20px;
    text-align: center;
    padding: 12px;
    height: 96px;
    width: calc(100% - 25px);
}

textarea::placeholder{
    color: #A5A5A5;
    font-family: Montserrat;
}

textarea:focus{
    outline-color: #61DAFA;
}

.TodoForm-buttonContainer{
    margin-top: 14px;
    display: flex;
    justify-content:space-between;
    align-items: center;
    width: 100%;

}

.TodoForm-button{
    cursor: pointer;
    display: inline-block;
    font-size: 20px;
    color: black;
    font-weight: 400;
    width: 120px;
    height: 48px;
    border-radius: 2px;
    border: none;
    font-family: Montserrat;
}

.TodoForm-button--add{
    background: #61DAFA;
    box-shadow: 0px 5px 25px rgba(97, 218, 250, 0.5);
}

.TodoForm-button--cancel{
    background: transparent;
}